import * as React from "react";
import Dialog from "@mui/material/Dialog";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import Slide from "@mui/material/Slide";
import { IoClose } from "react-icons/io5";
// import { TiArrowBack } from "react-icons/ti";
import styled from "styled-components";
import { useExport } from "../../../Context/Export";
import TableData from "../Pdf/TableData";
import Button from "@mui/material/Button";
const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function ExportData() {
  const exportData = useExport();

  React.useEffect(() => {}, [exportData.openBox]);

  const handleClose = () => {
    exportData.handleOpenBox(false);
  };

  return (
    <div>
      <Dialog
        fullScreen
        open={exportData.openBox}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        {/**
         * Top Tools Bar
         */}
        <ToolsBar />
        <MarkContainer>
          <TableData
            classInfo={exportData.classInfo}
            student={exportData.student}
          />
        </MarkContainer>
      </Dialog>
    </div>
  );
}

function ToolsBar() {
  const exportData = useExport();
  return (
    <AppBar
      sx={{
        position: "relative",
      }}
    >
      <Toolbar>
        <IconButton
          color="inherit"
          onClick={() => exportData.handleOpen(false)}
          aria-label="close"
        >
          <IoClose />
        </IconButton>
        <Typography
          sx={{
            ml: 2,
            flex: 1,
          }}
          variant="h6"
          component="div"
        >
          Export Data
        </Typography>
        {/* <Button autoFocus color="inherit" onClick={handleClose}>
       save
      </Button> */}
        <p
          className="courseTitle"
          style={{
            marginBottom: "0",
            marginRight: "2rem",
            textAlign: "center",
            width: "80%",
          }}
        >
          {exportData.classInfo.courseName +
            " - Term: " +
            exportData.classInfo.term}
        </p>

        <Button autoFocus color="inherit" onClick={() => {}}>
          Print
        </Button>
      </Toolbar>
    </AppBar>
  );
}

const MarkContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  margin-top: 0.5rem;
  overflow-y: scroll;
`;
