import React from "react";
import styled from "styled-components";
import { useExport } from "../../../Context/Export";

const TableData = () => {
  const exportData = useExport();

  return (
    <Container>
      <Img src={require("../../images/headerImage.png")} alt="BOU-SST" />
      <Information />
      <Table width="100%">
        <THead>{exportData.classInfo && <TableHead />}</THead>
        <TBody>
          {exportData.student.length > 0 &&
            exportData.student.map((data) => {
              return (
                <TableBodyRows
                  key={data.id}
                  data={data}
                  courseType={exportData.classInfo.courseType}
                />
              );
            })}
        </TBody>
      </Table>
    </Container>
  );
};

function TableBodyRows({ data, courseType }) {
  return (
    <TRow key={data.id}>
      <TData>{data.stuId}</TData>
      <TData style={{ whiteSpace: "nowrap" }}>{data.stuName}</TData>
      <TData style={{ textAlign: "center" }}>{data.attendance}</TData>
      {courseType === "Lab" ? <TData>{data.labPerform}</TData> : null}
      <TData>{data.assign1}</TData>
      <TData>{data.assign2}</TData>
      <TData>{data.assign3}</TData>
      <TData>{data.average}</TData>
      <TData>{data.test1}</TData>
      <TData>{data.test2}</TData>
      <TData>{data.best}</TData>
      <TData>{data.total}</TData>
    </TRow>
  );
}

const TableHead = ({ courseType }) => {
  return (
    <React.Fragment>
      <TRow>
        <TH rowSpan="2">Student ID</TH>
        <TH rowSpan="2">Student Name</TH>
        <TH rowSpan="2">Class Attendance</TH>
        {/**
         * Lab Performance
         */}
        {courseType === "Lab" ? <TH rowSpan="2">Lab Performance(10)</TH> : null}
        <TH colSpan="4">Assignment/Case Study (10)</TH>
        <TH colSpan="3">
          Quizzes/Class Test ({courseType === "Lab" ? 10 : 15})
        </TH>
        {/**
         * Total CA Marks
         */}
        <TH rowSpan="2">Total ({courseType === "Lab" ? 40 : 30})</TH>
      </TRow>
      <TRow>
        <TH>Assign-1</TH>
        <TH>Assign-2</TH>
        <TH>Assign-3</TH>
        {/**
         * Average Assignment Marks
         */}
        <TH>Average</TH>
        <TH>Test-1</TH>
        <TH>Test-2</TH>
        {/**
         * Best of Quizz/Test Marks
         */}
        <TH>Best</TH>
      </TRow>
    </React.Fragment>
  );
};

export default TableData;

const Container = styled.div`
  width: 90%;
`;

const Table = styled.table`
  font-size: 0.9rem;
  font-weight: 500;
  padding: 1px;
  border-radius: 5px;
  box-sizing: border-box;
  border-color: gray;
`;

const THead = styled.thead``;

const TBody = styled.tbody``;

const TRow = styled.tr``;

const TData = styled.td`
  border: 1px solid rgba(3, 3, 3, 0.3);
  padding: 0.3rem 0.5rem;
  &:nth-child(n + 4) {
    text-align: center;
  }
`;

const TH = styled.th`
  border: 1px solid rgba(3, 3, 3, 0.4);
  padding: 0.3rem 0.5rem;
  font-size: 0.8rem;
  text-align: center;
  font-weight: 600;
  min-width: 75px;
`;

const Img = styled.img`
  width: 100;
  height: auto;
  border-radius: 5px;
`;

const InformationPanel = styled.div`
  width: 100%;
`;

const Title = styled.h1`
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
`;

const Panel = styled.div`
  width: 100%;
`;

const SubPanel = styled.div`
  display: grid;
  gap: 0.5rem;

  margin-bottom: 1rem;
  &:nth-child(1) {
    grid-template-columns: auto auto auto;
  }

  &:nth-child(2) {
    grid-template-columns: auto auto;
  }
`;
const GroupText = styled.div`
  display: flex;
  flex-wrap: nowrap;
`;
const Label = styled.p`
  margin-bottom: 0.3rem;
  font-weight: 600,
  font-size: 1rem;
  width: 250px;
`;
const Text1 = styled.p`
  margin-bottom: 0.3rem;
  font-weight: 600,
  font-size: 1rem;
  border-bottom: 1px solid #333;
  width: 100%
`;

const Information = () => {
  const exportData = useExport();
  const classInfo = exportData.classInfo;
  return (
    <InformationPanel>
      <Title>B. Sc in Computer Science and Engineering Program</Title>
      <Title>Study Center: {classInfo.study_center}</Title>
      <Panel>
        <SubPanel>
          <GroupText>
            <Label>Year and Semester</Label>
            <Text1>: {getSemester(classInfo.year, classInfo.semester)}</Text1>
          </GroupText>
          <GroupText style={{ marginLeft: "5rem" }}>
            <Label>Session</Label>
            <Text1>: {classInfo.session}</Text1>
          </GroupText>
          <GroupText style={{ marginLeft: "2rem" }}>
            <Label>Term</Label>
            <Text1>: {classInfo.term}</Text1>
          </GroupText>
        </SubPanel>
        <SubPanel>
          <GroupText>
            <Label>Course Teacher</Label>
            <Text1>: {classInfo.teacherName}</Text1>
          </GroupText>
          <GroupText style={{ marginLeft: "2rem" }}>
            <Label>Course Name</Label>
            <Text1>: {classInfo.courseName}</Text1>
          </GroupText>
        </SubPanel>
        <SubPanel></SubPanel>
      </Panel>
    </InformationPanel>
  );
};

const getSemester = (a, b) => {
  if (a === 1) {
    return b === 1 ? "First Year First Semester" : "First Year Second Semester";
  } else if (a === 2) {
    return b === 1
      ? "Second Year First Semester"
      : "Second Year Second Semester";
  } else if (a === 3) {
    return b === 1 ? "Third Year First Semester" : "Third Year Second Semester";
  } else {
    return b === 1
      ? "Fourth Year First Semester"
      : "Fourth Year Second Semester";
  }
};
